.rules-style li::before {
  content: "";
  background-image: url("../../../public/images/event/gift-coin.webp");
  width: 30px;
  height: 25px;
  flex-shrink: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0px -3px;
}

.rules-style li {
  display: flex;
  align-items: start;
  gap: 5px;
  line-height: 1.4rem;
  margin-bottom: 1.5rem;
}

.main-fade-in-up {
  animation: fadeInUp 0.6s ease-out forwards;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(-90px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-sacle-L {
  animation: sacle-L 3s infinite;
}

@keyframes sacle-L {
  0% {
    transform: translate(0%, 0%) scale(1);
  }
  50% {
    transform: translate(-20%, -5%) scale(1.1);
  }
  100% {
    transform: translate(0%, 0%) scale(1);
  }
}
.animate-sacle-R {
  animation: sacle-R 3s infinite;
}
@keyframes sacle-R {
  0% {
    transform: translate(0%, 0%) scale(1);
  }
  50% {
    transform: translate(20%, -5%) scale(1.1);
  }
  100% {
    transform: translate(0%, 0%) scale(1);
  }
}
